@import 'variables';
@import 'mixins';

/* ==========================================================================
   #Comments styles - start
   ========================================================================== */

.eltdf-comment-holder {
    @include eltdfRelativeHolderLayout();
    padding: 0;
    box-sizing: border-box;

    .eltdf-comment-holder-inner {
        padding: 0 0 20px;
    }

    .eltdf-comment {

        &.eltdf-pingback-comment {

            .eltdf-comment-text {
                padding: 0;
            }
        }
    }

    .eltdf-comment-list {
        position: relative;
        padding: 0;
        margin: 0;
        list-style: none;
        z-index: 150;

        li {
            @include eltdfRelativeHolderLayout();
            padding: 86px 0 0;
        }

        > li:first-child {
            margin: 0;
            padding: 0;
            border: 0;
        }

        .children {
            padding: 0 0 0 103px
        }
    }

    .eltdf-comments-title {
        margin-bottom: 51px;

        > * {
            margin: 0;
        }
    }

    .eltdf-comment-image {
        float: left;

        img {
            width: 103px;
            border-radius: 50%;
        }
    }

    .eltdf-comment-text {
        padding: 0 0 0 131px;

        .eltdf-comment-info {
            @include eltdfRelativeHolderLayout();
            margin: 6px 0 7px;
        }

        .eltdf-comment-link{
            position: relative;
            float: right;
            margin-left: 10px;
            font-weight: 600;
            font-family: $default-heading-font;
            font-size: 13px;

            > a{
                margin-left: 10px;
                position: relative;
            }

            > a{
                @include eltdfAnimationLineVisible($bottom: -1px, $color: currentColor);

                &:hover {
                    @include eltdfAnimationLineHidden($bottom: -1px, $color: currentColor);
                }
            }
        }

        .replay,
        .comment-reply-link,
        .comment-edit-link {
        }

        .eltdf-comment-name {
            font-family: $default-text-font;
            font-size: 18px;
            font-weight: 600;
            margin: 0;
            line-height: 24px;
            display: block;
        }

        .eltdf-comment-date {
            display: block;
            font-size: 10px;
            text-transform: uppercase;
            letter-spacing: .16em;
            font-weight: 400;
        }

        .eltdf-text-holder{
            margin: 12px 0 0;
            line-height: 26px;

            > p {
                margin: 0;
            }
        }

        .comment-respond {
            margin: 50px 0;

            .comment-reply-title {
                margin: 0 0 30px;
                font-size: 18px;
                line-height: 24px;

                > small {
                    position: relative;
                    margin-left: 10px;
                    display: inline-block;
                    float: right;
                    line-height: 24px;

                    #cancel-comment-reply-link {
                        line-height: 19px;
                        font-size: 13px;
                        @include eltdfAnimationLineVisible($color: currentColor);

                        &:hover {
                            @include eltdfAnimationLineHidden($color: currentColor);
                        }
                    }
                }
            }

            .logged-in-as {
                margin: 0 0 8px;
            }
        }

        ul, ol {

            li {
                display: list-item;
            }
        }

        li {
            margin: 0 !important;
            padding: 0 !important;
            border: 0 !important;
        }
    }
}

.eltdf-comment-form, .eltdf-comment-holder{
    input#author{
        float: left;
        width: 49%;
        margin-right: 2%;
    }

    input#email{
        float: left;
        width: 49%;
    }
}

.eltdf-comment-form {
    padding: 0;
    clear: both;
    box-sizing: border-box;

    .eltdf-comment-form-inner {
        padding: 90px 0 54px;
    }

    .comment-reply-title {
        margin: 0 0 43px;
    }

    .logged-in-as {
        margin: 0 0 8px;
    }
}

@include ipad-portrait {

    .eltdf-comment-holder {

        .eltdf-comment-list {

            .children {
                padding: 0 0 0 40px;
            }
        }
    }
}

@include phone-landscape {

    .eltdf-comment-holder {

        .eltdf-comment-list {

            .children {
                padding: 0;
            }
        }
    }
}

@include phone-portrait {

    .eltdf-comment-holder {

        .eltdf-comment-image {
            float: none;
        }

        .eltdf-comment-text {
            padding: 20px 0 0;
            min-height: inherit;
        }
    }
}

/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

#respond textarea,
.eltdf-style-form textarea,
#respond input[type='text'],
input[type='text'],
input[type='email'],
input[type='password'],
.post-password-form input[type='password'] {
    @extend %input-style;
}

.post-password-form input[type='password'] {
    margin-top: 10px;
}

.wpcf7-form-control {
    &.wpcf7-text,
    &.wpcf7-number,
    &.wpcf7-date,
    &.wpcf7-textarea,
    &.wpcf7-select,
    &.wpcf7-quiz {
        @extend %input-style;

        footer & {
            margin: 0;
        }
    }
}

#submit_comment,
.post-password-form input[type='submit'],
input.wpcf7-form-control.wpcf7-submit {
    @include eltdfButtonDefaultStyle();
    @include eltdfButtonSize();
    @include eltdfButtonSolidColor();
    @include eltdfAnimationLineVisible($bottom: -4px);

    cursor: pointer;

    &:hover {
        @include eltdfButtonSolidHoverColor();
        @include eltdfAnimationLineHidden($bottom: -4px);
    }
}

.post-password-form input[type='submit'],
input#submit_comment {
    &:hover {
        color: #fff !important;
        background-color: $second-heading-color !important;
    }
}

.wpcf7-form p{
    margin: 0;
    //display: inline-block;
    position: relative;
}

p.form-submit{
    margin: 0;
    display: inline-block;
    position: relative;
    @include eltdfAnimationLineVisible($bottom:-4px);

    &:hover{
        @include eltdfAnimationLineHidden($bottom:-4px);
    }
}

#respond textarea,
.eltdf-style-form textarea,
.wpcf7-form textarea {
    display: block;
    border:0;
    background-color: $additional-background-color !important;
    border-bottom: 0 !important;
}

textarea#comment{
    min-height: 200px;

    @include eltdfPlaceholder {
        text-transform: uppercase;
        font-weight: 400;
    }
}

.eltdf-two-columns-form-without-space {
    .wpcf7-form .ajax-loader {
        display: none;
    }
}

.eltdf-our-offices-form {
    .wpcf7-submit {
        width: 100%;
    }
}

.wpcf7-form .ajax-loader {
    position: absolute;
    right: 0;
    top: 50%;
    @include eltdfTransform(translate(150%, -50%));
}

#text-center{
    text-align: center;
}

/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

/* ==========================================================================
   #Global Owl Slider - begin
   ========================================================================== */
.eltdf-owl-slider {
    visibility: hidden;

    .owl-stage-outer {
        z-index: 1;
    }

    &.eltdf-slider-is-moving {
        .owl-item {
            .eltdf-block-drag-link {
                z-index: -1 !important;
            }
        }
    }

    &.eltdf-slider-has-both-nav {
        .owl-nav {
            .owl-prev,
            .owl-next {
                $pos: calc(-50% - 21px); // 21 is is half height of pagination control + top margin
                @include eltdfTransform(translateY($pos));
            }
        }
    }

    &.eltdf-slider-hide-pagination .owl-dots {
        display: none;
    }

    .owl-nav {

        $owl-height: 57px;

        .owl-prev,
        .owl-next {
            position: absolute;
            bottom: 0;
            height: $owl-height;
            line-height: $owl-height;
            text-align: center;
            width: $owl-height;
            padding: 0;
            margin: 0;
            background-color: #fff;
            color: #000;
            border: 0;
            border-radius: 0;
            outline: none;
            -webkit-appearance: none;
            z-index: 3;
            overflow: visible;

            > span {
                position: relative;
                display: inline-block;
                vertical-align: middle;
                font-size: 20px;
                line-height: 1;
	
	            //@include eltdfTransitionTransform(.2s cubic-bezier(0.2, 0.14, 0.09, 1.25) .25s);
	            @include eltdfTransition(opacity .2s);
	            -webkit-backface-visibility: hidden;
	            backface-visibility: hidden;

                &:before {
                    display: block;
                    line-height: inherit;
                }
            }
        }

        .owl-prev {
            right: $owl-height;
            
            &:hover {
                > span {
	                opacity: .7;
                }
            }
        }

        .owl-next {
            right: 0;
    
            &:hover {
                > span {
	                opacity: .7;
                }
            }
        }
    }

    .owl-dots {
        text-align: center;
        margin: 16px 0 0;

        .owl-dot {
            display: inline-block;
            vertical-align: middle;
            padding: 0;
            margin: 0 7px;
            background: none;
            border: 0;
            border-radius: 0;
            outline: none;
            overflow: visible;
            -webkit-appearance: none;

            span {
                display: inline-block;
                vertical-align: middle;
	            height: 10px;
	            width: 10px;
                margin: 0;
                border-radius: 50%;
                background-color: $default-heading-color;
                border: 1px solid transparent;
                box-sizing: border-box;
	            @include eltdfTransform(scale(.4));
                @include eltdfTransition(transform .2s ease-in, border .3s ease-in);
            }

            &:hover,
            &.active {
                span {
                    @include eltdfTransform(scale(1));
                    background-color: transparent;
                    border-color: $default-heading-color;
	                @include eltdfTransition(transform .3s ease-out, border .2s ease-out);
                }
            }

        }
    }

    .owl-carousel .owl-item img {
        -webkit-transform: translateZ(0); //chrome text cut off fix
    }

    + .eltdf-slider-thumbnail {
        display: inline-block;
        vertical-align: middle;
        list-style: none;
        margin: 20px -10px 0;

        > .eltdf-slider-thumbnail-item {
            float: left;
            padding: 0 10px;
            margin-bottom: 20px;
            box-sizing: border-box;
            line-height: 0;
            cursor: pointer;

            img {
                border: 2px solid transparent;

                @include eltdfTransition(border-color 0.2s ease-out);
            }

            &.active {
                img {
                    border: 2px solid $first-main-color;
                }
            }
        }

        $thumbnail_columns_number: ('two', 'three', 'four', 'five', 'six');

        @for $i from 0 to length($thumbnail_columns_number) {
            &.eltdf-slider-columns-#{nth($thumbnail_columns_number,$i+1)} {

                .eltdf-slider-thumbnail-item {
                    width: 100% / ($i+2);

                    @include ipad-landscape {
                        width: 33.3333%;
                    }
                }
            }
        }
    }
}

/* ==========================================================================
   #Global Owl Slider - end
   ========================================================================== */

/* ==========================================================================
   #404 styles - start
   ========================================================================== */

.error404 {
    .eltdf-wrapper {
        padding: 0 !important;
    }

    .eltdf-content {
        height: 100vh;
    }

    &.admin-bar {
        .eltdf-content {
            height: calc(100vh - 32px);
        }
    }

    .eltdf-content-inner {
        @include eltdfTableLayout();
    }

    .eltdf-page-not-found {
        @include eltdfTableCellLayout();

        padding: 0 20%;
        text-align: center;

        .eltdf-404-title {
            margin: 0;
        }

        .eltdf-404-subtitle {
            margin: 35px 0 0;
        }

        .eltdf-404-text {
            margin: 10px 0 0;
        }

        .eltdf-btn {
            margin: 30px 0 0;

            &.eltdf-btn-light-style {
                color: #585858;
                background-color: #fff;
                border-color: #fff;

                &:hover {
                    color: #fff !important;
                    background-color: transparent !important;
                    border-color: #fff !important;
                }
            }
        }
    }

    @include ipad-landscape {
        .eltdf-content {
            height: 100%;
            min-height: 100vh;
        }

        &.admin-bar {
            .eltdf-content {
                height: 100%;
                min-height: calc(100vh - 32px);
            }
        }
    }

    @include ipad-portrait {
        .eltdf-page-not-found {
            padding: 70px 30px 20px;
        }
    }
}

/* ==========================================================================
   #404 styles - end
   ========================================================================== */

/* ==========================================================================
   #Back to Top styles - start
   ========================================================================== */
#eltdf-back-to-top {
    position: fixed;
    right: 0;
    bottom: 35px;
    margin: 0;
    z-index: 10000;
    opacity: 0;
	font-size: 15.2px;
	font-family: $default-heading-font;
	font-weight: 600;
    visibility: hidden;
	transform-origin: top;
	@include eltdfTransform(rotate(270deg));
	
	@include eltdfAnimationLineVisible($bottom:1px, $color:currentColor);

    &.off {
        opacity: 0;
        visibility: hidden;

        @include eltdfTransition(opacity 0.15s ease, visibility .0s .15s);
    }

    &.on {
        opacity: 1;
        visibility: visible;

        @include eltdfTransition(opacity 0.3s ease);
    }
	
	&:hover {
		@include eltdfAnimationLineHidden($bottom:1px, $color:currentColor);
	}

    &.eltdf-back-to-top-light {
        color: #fff;
    }
}

/* ==========================================================================
   #Back to Top styles - end
   ========================================================================== */

/* ==========================================================================
   #Self Hosted Video styles - begin
   ========================================================================== */
.eltdf-self-hosted-video-holder {
    .eltdf-video-wrap {
        .mejs-poster,
        .mejs-container,
        .mejs-overlay-play {
            height: 100% !important; /*!important because of the inline style*/
            width: 100% !important; /*!important because of the inline style*/
        }
    }
}

/* ==========================================================================
   #Self Hosted Video styles - end
   ========================================================================== */

/* ==========================================================================
   #Search tweak begin
   ========================================================================== */

// WEBKIT
input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-results-button,
input[type="search"]::-webkit-search-results-decoration {
    display: none;
}

// IE
input[type="search"]::-ms-clear {
    display: none;
}

input[type=text]::-ms-clear {
    display: none;
}

/* ==========================================================================
   #Search tweak end
   ========================================================================== */

.eltdf-rev-social-icons {
    position: relative;

    &:after {
        content: '';
        background-color: currentColor;
        height: 1px;
        width: 100%;
        position: absolute;
        top: 50%;
        left: 0;
        opacity: 0;
        @include eltdfTransition(opacity 0.15s ease-in-out);
    }

    &:hover {
        &:after {
            opacity: 1;
            @include eltdfTransition(opacity 0.15s ease-in-out);
        }
    }
}

.eltdf-bulleted-list {
    list-style: none;

    li {
        margin: 13px 0;

        &:before {
            content: "\2022";
            color: #f6dcd7;
            font-weight: 700;
            vertical-align: top;
            display: inline-block;
            width: 1.1em;
            margin-left: -.12em;
            font-size: 22px;
        }
    }
}

.eltdf-underlined-text {
    @include eltdfAnimationLineVisible($bottom: -2px);
}

.eltdf-strikethrough-links {
	position: relative;
	@include eltdfAnimationLineHidden($bottom: -2px, $color: currentColor);
	
	&:hover {
		@include eltdfAnimationLineVisible($bottom: -2px, $color: currentColor);
	}
}

.eltdf-bl-holder {
	position: absolute !important;
	bottom:0;
	display:block !important;
	width:100% !important;
	height: 0.611em;
	overflow:hidden;
	font-family: $default-heading-font;
	font-size:229px;
	font-weight:600;
	line-height:0.751em !important;
	text-align:center;
	color: rgba(255,255,255,.6);
	
	.wpb_content_element {
		transform-origin:bottom;
		@include eltdfTransform(translateY(50%) scaleY(0));
		@include eltdfTransitionTransform( .7s cubic-bezier(0.27,0, 0, 0.89));
	}
	
	@include laptop-landscape-large {
		font-size: 160px;
	}
	@include laptop-landscape-medium {
		font-size: 140px;
	}
	@include ipad-landscape {
		font-size: 90px;
	}
}

.eltdf-bl-holder.letterspopped {
	.wpb_content_element {
		@include eltdfTransform(translateY(0) scaleY(1));
	}
}