/* ==========================================================================
   Sidebar area styles - begin
   ========================================================================== */

aside.eltdf-sidebar {
    position: relative;
    z-index: 11;
    margin: 0 0 60px;

    .widget {

        &:last-child {
            margin: 0 !important;
        }
    }
}

.wpb_widgetised_column {
    position: relative;

    .widget {

        &:last-child {
            margin: 0 !important;
        }
    }
}

/* ==========================================================================
   Sidebar area styles - end
   ========================================================================== */

/* ==========================================================================
   Default WordPress Widgets styles - begin
   ========================================================================== */

.widget {
    margin: 0 0 40px;

    .select2-container--default {
        text-align: left;
        text-transform: capitalize;
        font-size: 13px;
        letter-spacing: normal;
        border-bottom: 1px solid #000;

        .select2-selection__rendered {
            padding-left: 0 !important;
        }
    }

    .eltdf-widget-title {
        margin: 0 0 20px;
        font-size: 18px;;

        a {
            color: inherit;
        }
    }

    ul {
        padding: 0;
        margin: 0;
        list-style: none;
        font-size: 13px;
        font-weight: 400;

        li {
            margin: 0 0 9px;

            &:last-child {
                margin: 0;
            }
        }

        &.children {
            margin: 9px 0 0 9px;
        }
    }

    /*
    *   Specific styles
    */
    &.widget_text {

        img {
            display: block;
        }
    }

    &.widget_shopping_cart {

        .woocommerce-mini-cart__buttons {
            @include eltdfClearfix();
        }
    }

    #wp-calendar {
        width: 100%;
        text-align: center;

        caption {
            color: $default-heading-color;
            margin: 0 0 15px;
        }

        th, td {
            padding: 3px 4px;
        }

        td#today {
            color: #fff;
            background-color: $first_main_color;

            a {
                color: inherit;
            }
        }
    }

    &.eltdf-recent-post-widget {

        .eltdf-widget-title {
            margin: 0 0 32px;
        }

        ul {

            li {
                margin: 0 0 20px;

                a {
                    @include eltdfTableLayout();

                    .eltdf-rp-image {
                        @include eltdfTableCellLayout();
                        width: 72px;
                        height: 72px;

                        img {
                            display: block;
                        }
                    }

                    .eltdf-rp-text {
                        @include eltdfTableCellLayout();
                        padding: 0 0 0 15px;

                        .eltdf-rp-title {
                            margin: 0 0 7px;
                            font-weight: 500;
                        }

                        p {
                            margin: 0;
                            font-size: 10px;
                            text-transform: uppercase;
                            font-weight: 400;
                            letter-spacing: .16em;
                            line-height: 18px;
                        }
                    }
                }
            }
        }
    }

    &.widget_rss {

        .eltdf-widget-title {

            .rsswidget {

                &:hover {
                    color: $first-main-color;
                }
            }
        }

        .rss-widget-icon {
            display: inline-block;
        }

        ul {

            li {
                margin: 0 0 25px;

                .rss-date {
                    display: block;
                }
            }
        }
    }

    &.eltdf-contact-form-7-widget {

        .input-button-wrapper {
            width: 100%;

            input {
                width: 100% !important;
            }
        }
    }

    &.eltdf-single-line-form {
        margin: 0 0 51px;

        .eltdf-page-footer:not(.eltdf-footer-dark-text) & {
            form {

                > p {
                    border-bottom: 1px solid #fff;
                }
            }

            p > .wpcf7-form-control-wrap > input,
            p > .input-single-line-button-wrapper {
                color: #fff !important;

            }

            p > .input-single-line-button-wrapper {

                &:hover {
                    color: #fff;
                }
            }
        }

        form {
            @include eltdfRelativeHolderLayout();
            clear: both;

            > p {
                @include eltdfRelativeHolderLayout();
                border-bottom: 1px solid $default-border-color;
                box-sizing: border-box;
                @include eltdfTransition(border-color 0.2s ease-in-out);
    
                &:focus-within {
                    color: $default-heading-color;
                    border-color: $default-text-color;
                }
            }
        }

        .wpcf7-not-valid-tip {
            display: none !important;
        }

        p > .wpcf7-form-control-wrap > input,
        p > .input-single-line-button-wrapper {
            position: relative !important;
            display: inline-block;
            vertical-align: top;
            float: left;
            margin: 0 !important;
            height: 40px;
            line-height: 32px !important;
            color: $default-text-color !important;
            background-color: transparent !important;
            border: 0 !important;
            border-radius: 0;
            outline: 0;
            -webkit-appearance: none;
            box-sizing: border-box;
            @include eltdfTransition(color .2s ease-out);

        }

        p > .wpcf7-form-control-wrap > input {
            width: calc(100% - 40px) !important;
            font-family: inherit !important;
            font-size: 10px !important;
            letter-spacing: .160em !important;
            text-transform: uppercase !important;
            padding: 1px !important;
            font-weight: 400 !important;
            cursor: text !important;

            @include eltdfPlaceholder {
                color: inherit;
            }
        }

        p > .input-single-line-button-wrapper {
            width: 40px !important;
            padding: 0 !important;
            font-size: 16px !important;
            cursor: pointer !important;
            text-align: right !important;

            &:hover {
                color: $default-heading-color;
            }

            &:before {
                display: block;
                line-height: 40px;
                font-family: 'Font Awesome 5 Free';
                content: "\f0e0";
                font-size: 15px;
                position: relative;
            }
        }

        p > .input-single-line-button-wrapper > input {
            position: absolute !important;
            right: 0 !important;
            top: 0 !important;
            height: 100%;
            width: 40px !important;
            padding: 0 !important;
            font-size: 0;
            opacity: 0;
        }
    }

    &.widget_search {
        margin: 0 0 51px;

        form {
            @include eltdfRelativeHolderLayout();
            clear: both;

            > div {
                @include eltdfRelativeHolderLayout();
                border-bottom: 1px solid $default-border-color;
                box-sizing: border-box;
                @include eltdfTransition(border-color 0.2s ease-in-out);
    
                &:focus-within {
                    border-bottom-color: $first-main-color;
                }
            }
        }

        input,
        button {
            position: relative;
            display: inline-block;
            vertical-align: top;
            float: left;
            margin: 0;
            height: 40px;
            line-height: 32px;
            color: $default-text-color;
            background-color: transparent;
            border: 0;
            border-radius: 0;
            outline: 0;
            -webkit-appearance: none;
            box-sizing: border-box;
            @include eltdfTransition(color .2s ease-out);
        }

        input {
            width: calc(100% - 40px);
            font-family: inherit;
            font-size: 10px;
            letter-spacing: .160em;
            text-transform: uppercase;

            @include eltdfPlaceholder {
                color: inherit;
            }
        }

        button {
            width: 40px;
            padding: 0;
            font-size: 16px;
            cursor: pointer;
            text-align: right;

            &:hover {
                color: $default-heading-color;
            }

            span {
                display: block;
                line-height: inherit;

                &:before {
                    display: block;
                    line-height: inherit;
                }
            }
        }
    }

    &.widget_pages,
    &.widget_archive,
    &.widget_categories,
    &.widget_meta,
    &.widget_recent_comments,
    &.widget_nav_menu {

        ul {

            li {
                margin: 0;
                padding: 4px 0;

                &:last-child {
                    border: none;
                }

                ul {
                    margin: 4px 0 0;
                    padding: 0 0 0 10px;

                    li {

                        &:last-child {
                            padding: 4px 0 0;
                        }
                    }
                }
            }
        }
    }

    &.widget_recent_entries {

        .eltdf-widget-title {
            margin: 0 0 23px;
        }

        ul {

            li {
                margin: 0 0 12px;

                > span {
                    display: block;
                    font-size: 14px;
                }
            }
        }
    }

    &.widget_archive,
    &.widget_categories,
    &.widget_text {

        select {
            width: 100%;
            max-width: 100%;
            height: 54px;
            padding: 0 18px;
            line-height: 54px;
            color: $default-text-color;
            background-color: transparent;
            border: 1px solid $default-border-color;
            font-family: inherit;
            font-size: inherit;
            font-style: inherit;
            font-weight: inherit;
            outline: none;
            box-sizing: border-box;
        }
    }

    &.posts_holder {

        ul {

            li {

                &.page_item_has_children {

                    & > a {
                        display: block;
                    }

                    & > ul.children {
                        padding-left: 10px;
                    }
                }

                &.menu-item-has-children {

                    & > a {
                        display: block;
                    }

                    & > ul.sub-menu {
                        padding-left: 10px;
                    }
                }
            }
        }
    }

    &.widget_categories {

        .eltdf-widget-title {
            margin: 0 0 15px;
        }

    }

    &.widget_tag_cloud {

        .eltdf-widget-title {
            margin: 0 0 26px;
        }

        .tagcloud {

            a {
                position: relative;
                display: inline-block;
                vertical-align: top;
                font-family: inherit;
                background-color: transparent;
                font-size: 10px !important;
                text-transform: uppercase;
                font-weight: 500;
                letter-spacing: .160em;
                line-height: 18px;

                &:after {
                    content: ',';
                    color: $default-text-color;
                }

                &:last-child {

                    &:after {
                        display: none;
                    }
                }
            }
        }
    }

    &.widget_media_image {
        margin: 0 0 18px;

        a, img {
            display: block;
        }
    }
}

.vc_wp_search {

    .widget {

        &.widget_search {

            form {

                > div {
                    background-color: #fff;
                    border: none;
                }
            }

            input {
                width: calc(100% - 66px);
                padding: 0 0 0 33px;
                font-size: 15px;
            }

            button {
                width: 66px;
                padding: 0 39px 0 9px;
            }
        }
    }
}

.eltdf-page-header,
.eltdf-top-bar,
.eltdf-sticky-header,
.eltdf-mobile-header {

    .widget {
        margin: 0;

        &.widget_pages,
        &.widget_archive,
        &.widget_categories,
        &.widget_meta,
        &.widget_recent_entries,
        &.widget_recent_comments,
        &.widget_rss,
        &.widget_calendar,
        &.widget_rss,
        &.widget_archive,
        &.widget_categories,
        &.widget_tag_cloud,
        &.widget_nav_menu,
        &.widget_search {
            display: none !important;
        }

        @for $i from 0 to 50 {
            &#monster-widget-placeholder-#{$i} {
                display: none;
            }
        }
    }
}

.eltdf-side-menu {

    .widget {
        color: #000;

        &:last-child {
            margin: 0 !important;
        }

        h1, h2, h3, h4, h5, h6 {
            margin: 0;
            color: #fff;
        }

        .eltdf-widget-title {
            color: $default-heading-color;
            margin: 0 0 27px;
        }

        a {
            color: #000;

            &:hover {
                color: $first-main-color;
            }
        }

        table thead tr,
        table tbody tr {
            border-color: #000;
        }

        table tbody th {
            border-right-color: #000;
        }

        /*
        *   Specific styles
        */
        #wp-calendar {

            caption {
                color: #000;
            }
        }

        &.widget_rss {

            .eltdf-footer-widget-title {

                .rsswidget {
                    color: #fff;

                    &:hover {
                        color: $first-main-color;
                    }
                }
            }
        }

        &.widget_search {

            form {

                > div {
                    border-color: #000;
                }
            }

            input {
                color: #000;

                @include eltdfPlaceholder {
                    color: inherit;
                }

                &[type='submit'] {
                    color: #000;
                }
            }

            button {

                &:hover {
                    color: $first-main-color;
                }
            }
        }

        &.widget_archive,
        &.widget_categories,
        &.widget_text {

            select {
                color: #000;
                border-color: #000;

                option {
                    background-color: #363636;
                }
            }

        }

        &.widget_tag_cloud {

            a {
                color: #000;

                &:after {
                    color: inherit;
                }

                &:hover {
                    color: $first-main-color;
                }
            }
        }
    }
}

.eltdf-page-footer {
    $footer_text_color: #fff;

    .widget {
        color: $footer_text_color;
        font-size: 13px;
        line-height: 20px;
        margin: 0 0 20px;

        &:last-child {
            margin: 0 !important;
        }

        h1, h2, h3, h4, h5, h6 {
            margin: 0;
            color: #fff;
        }

        .eltdf-widget-title {
            color: #fff;
            margin: 0 0 27px;
        }

        a {
            color: $footer_text_color !important;

            &:hover {
                color: #b1b2b2 !important;
            }
        }

        table thead tr,
        table tbody tr {
            border-color: $footer_text_color;
        }

        table tbody th {
            border-right-color: $footer_text_color;
        }

        /*
        *   Specific styles
        */
        .select2-container--default.select2-container--open .select2-selection--single {
            color: #fff;
        }

        .select2-container--default {
            border-color: $footer_text_color;
        }

        del {
            color: rgba(255, 255, 255, .3) !important;
        }

        .star-rating {
            color: #fff;
        }

        &.woocommerce.widget_product_search .woocommerce-product-search button, &.woocommerce.widget_product_search .woocommerce-product-search input, &.widget_search button, &.widget.widget_search input {
            color: $footer_text_color;

            &:hover {
                color: #b1b2b2;
            }
        }

        #wp-calendar {

            caption {
                color: $footer_text_color;
            }
        }

        &.widget_text {
            margin: 0 0 22px;
        }

        &.widget_rss {

            .eltdf-footer-widget-title {

                .rsswidget {
                    color: #fff;

                    &:hover {
                        color: #b1b2b2;
                    }
                }
            }
        }

        &.widget_search {

            form {

                > div {
                    border-color: $footer_text_color;
                }
            }

            input {
                color: $footer_text_color;

                @include eltdfPlaceholder {
                    color: inherit;
                }

                &[type='submit'] {
                    color: $footer_text_color;
                }
            }

            button {

                &:hover {
                    color: #b1b2b2;
                }
            }
        }

        &.widget_archive,
        &.widget_categories,
        &.widget_text {

            select {
                color: $footer_text_color;
                border-color: $footer_text_color;

                option {
                    background-color: #363636;
                }
            }

        }

        &.widget_tag_cloud {

            a {
                color: $footer_text_color;

                &:after {
                    color: inherit;
                }

                &:hover {
                    color: #b1b2b2;
                }
            }
        }

        &.widget_archive, &.widget_categories, &.widget_meta, &.widget_nav_menu, &.widget_pages, &.widget_recent_comments {
            ul li {
                padding: 0 0 13px;
            }
        }

        &.eltdf-contact-form-7-widget {
            margin-top: -7px;
        }

        .eltdf-single-line-form p > .input-single-line-button-wrapper,
        .eltdf-single-line-form p > .wpcf7-form-control-wrap > input {
            height: 37px;
        }
    }

    &.eltdf-footer-dark-text {
        $footer_text_color: #000;

        .widget {

            color: $footer_text_color;

            &:last-child {
                margin: 0 !important;
            }

            h1, h2, h3, h4, h5, h6 {
                margin: 0;
                color: #fff;
            }

            .eltdf-widget-title {
                color: #000;
                margin: 0 0 27px;
            }

            a {
                color: $footer_text_color !important;

                &:hover {
