            }

            table tbody th {
                border-right-color: $footer_text_color;
            }

            /*
            *   Specific styles
            */
            .select2-container--default.select2-container--open .select2-selection--single {
                color: #000;
            }

            .select2-container--default {
                border-color: $footer_text_color;
            }

            del {
                color: rgba(0, 0, 0, .3) !important;
            }

            .star-rating {
                color: #000;
            }

            &.woocommerce.widget_product_search .woocommerce-product-search > div {
                border-bottom: 1px solid #000;
            }

            &.woocommerce.widget_product_search .woocommerce-product-search button, &.woocommerce.widget_product_search .woocommerce-product-search input, &.widget_search button, &.widget.widget_search input {
                color: $footer_text_color;

                &:hover {
                    color: #000;
                }
            }

            #wp-calendar {

                caption {
                    color: $footer_text_color;
                }
            }

            &.widget_rss {

                .eltdf-footer-widget-title {

                    .rsswidget {
                        color: #000;

