                    color: $footer_text_color;
                    border-color: $footer_text_color;

                    option {
                        background-color: #363636;
                    }
                }

            }

            &.widget_tag_cloud {

                a {
                    color: $footer_text_color;

                    &:after {
                        color: inherit;
                    }

                    &:hover {
                        color: #000;
                    }
                }
            }
        }
    }
}

.eltdf-top-bar {

    a {
        color: #969696;

        &:hover {
            color: #fff;
        }
    }

    .widget {
        display: inline-block;
        vertical-align: middle;
        margin: 0;

        p {
            margin: 0;
        }

        &.widget_text {
            margin: 0 8px 0 0;
        }

        &.widget_nav_menu {

            ul {
                clear: both;

                li {
                    float: left;
                    padding: 0 8px;
                    margin: 0;
                    line-height: 14px;
                    border-left: 1px solid #fff;
                }
            }
        }
    }
}

/* ==========================================================================
   Default WordPress Widgets styles - end
   ========================================================================== */