/* ==========================================================================
   Select 2 Script styles - begin
   ========================================================================== */

/*
	Version: 4.0.3 Timestamp: Monday Mart 27 2:59:00pm EDT 2017
*/
body .select2-container--default { /* just to make sure that this code overwrite woocommerce css */
    position: relative;
    display: inline-block;
    height: $sselect-input-height;
    width: auto;
    margin: 0 0 $input-margin;
    vertical-align: middle;
    font-family: $default-text-font;
    font-size: 10px;
    text-transform: uppercase;
    text-align: left;
    border: 0;
    border-radius: 0;
    box-sizing: border-box;
    letter-spacing: .160em;
    font-weight: 400;
    z-index: 9999;
    
    &[dir="rtl"] {
        
        .select2-selection--single {
            
            .select2-selection__clear {
                float: left;
            }

            .select2-selection__rendered {
                padding: 0 20px 0 30px;
            }

            .select2-selection__arrow {
                left: 20px;
                right: auto;
            }
        }

        .select2-selection--multiple {
            
            .select2-selection__choice,
            .select2-selection__placeholder,
            .select2-search--inline {
                float: right;
            }

            .select2-selection__rendered {
                padding: 0 20px 0 30px;
            }

            .select2-selection__choice {
                margin: 7px auto 0 5px;
            }

            .select2-selection__choice__remove {
                margin: 0 auto 0 3px;
            }
        }
    }

    &.select2-container--disabled {
        
        .select2-selection--single {
            background-color: #eee;
            cursor: default;

            .select2-selection__clear {
                display: none;
            }
        }

        .select2-selection--multiple {
            background-color: #eee;
            cursor: default;
        }

        .select2-selection__choice__remove {
            display: none;
        }
    }

    &.select2-container--open {
        
        .select2-selection--single {
            color: $default-heading-color;
        }

        .select2-dropdown {
            left: 0;

            &.select2-dropdown--above {
                border-bottom-color: transparent;
            }

            &.select2-dropdown--below {
                border-top-color: transparent;
            }
        }
    }

    .select2-selection--single {
        display: block;
        height: $sselect-input-height;
        padding: 0;
        font-family: inherit;
        font-size: inherit;
        line-height: #{$sselect-input-height};
        color: inherit;
        background-color: transparent;
        border: none;
        border-radius: 0;
        cursor: pointer;
        user-select: none;
        -webkit-user-select: none;
        box-sizing: border-box;

        .select2-selection__rendered {
            display: block;
            padding: 0 #{$input-horizontal-padding + 10px} 0 $input-horizontal-padding; // add 10px on right side, makes room for arrow
            color: inherit;
            line-height: inherit;
            text-overflow: ellipsis;
            overflow: hidden;
            white-space: nowrap;
            box-sizing: border-box;
        }

        .select2-selection__clear {
            position: relative;
            float: right;
            margin: 0;
            font-weight: inherit;
            cursor: pointer;
        }

        .select2-selection__placeholder {
            color: inherit;
        }

        .select2-selection__arrow {
            position: absolute;
            right: 0;
            top: 0;
            width: auto;
            height: #{$sselect-input-height - 2px};
            color: inherit;
            line-height: #{$sselect-input-height - 6px};
            text-align: center;
            font-size: 22px;

            b {
                display: none;
            }

            &:before {
                content: '\33';
                font-family: 'ElegantIcons';
                font-style: normal;
            }
        }
    }

    .select2-selection--multiple {
        display: block;
        height: 44px;
        padding: 0;
        font-family: inherit;
        font-size: inherit;
        line-height: 42px;
        color: inherit;
        background-color: transparent;
        border: 1px solid $default-border-color;
        border-radius: 0;
        outline: 0;
        cursor: pointer;
        user-select: none;
        -webkit-user-select: none;
        box-sizing: border-box;

        .select2-selection__rendered {
            display: block;
            margin: 0;
            padding: 0 30px 0 20px;
            list-style: none;
            color: inherit;
            line-height: inherit;
            text-overflow: ellipsis;
            overflow: hidden;
            white-space: nowrap;
            box-sizing: border-box;

            li {
                list-style: none;
            }

            .select2-selection__placeholder {
                float: left;
                margin: 0;
                color: inherit;
            }

            .select2-selection__clear {
                position: relative;
                float: right;
                margin: 0;
                font-weight: inherit;
                cursor: pointer;
            }

            .select2-selection__choice {
                float: left;
                margin: 7px 5px 0 0;
                padding: 0 10px;
                line-height: 30px;
                color: #fff;
                background-color: $default-heading-color;
                border: 0;
                border-radius: 0;
                cursor: default;
            }

            .select2-selection__choice__remove {
                display: inline-block;
                margin: 0 3px 0 0;
                font-weight: 700;
                color: inherit;
                cursor: pointer;
                @include eltdfTransition(opacity .2s ease-out);

                &:hover {
                    color: inherit;
                    opacity: 0.8;
                }
            }
        }
    }

    .select2-search--inline {
        float: left;

        .select2-search__field {
            margin: 0;
            padding: 0;
            font-size: 100%;
            background: transparent;
            border: none;
            outline: 0;
            box-shadow: none;
            -webkit-appearance: textfield;
            box-sizing: border-box;

            &::-webkit-search-cancel-button {
                -webkit-appearance: none;
            }
        }
    }

    .select2-dropdown {
        position: absolute;
        top: 0;
        left: -100000px;
        width: 100%;
        display: block;
        background-color: $additional-background-color;
        border: 0;
        border-radius: 0;
        z-index: 1051;
        box-sizing: border-box;
    }

    .select2-search--dropdown {
        display: block;
        padding: 14px 15px 2px;

        &.select2-search--hide {
            display: none;
        }

        .select2-search__field {
            margin: 0;
            padding: 4px 8px;
            width: 100%;
            font-family: inherit;
            font-size: inherit;
            font-weight: inherit;
            line-height: 20px;
            color: inherit;
            border: 1px solid $default-border-color;
            outline: none;
            -webkit-appearance: none;
            box-sizing: border-box;

            &:focus {
                border-color: $default-heading-color;
            }

            &::-webkit-search-cancel-button {
                -webkit-appearance: none;
            }
        }
    }

    .select2-results {
        display: block;
        margin: 0;
        padding: 0;
        overflow: hidden;
        font-size: 13px;
        text-align: left;
        font-weight: 400;
        letter-spacing: 0;
        text-transform: capitalize;

        > .select2-results__options {
            max-height: 200px;
            overflow-y: auto;
        }
    }

    .select2-results__options {
        position: relative;
        list-style: none;
        margin: 5px 0;
        padding: 2px 24px;
    }

    .select2-results__option {
        padding: 7px 0;
        user-select: none;
        -webkit-user-select: none;
        @include eltdfTransition(color .2s ease-out);
        
        &[aria-selected] {
            cursor: pointer;
        }

        &[role=group] {
            padding: 0;
        }
        
        &[aria-disabled=true] {
            color: $default-heading-color;
        }

        &[aria-selected=true],
        &[data-selected=true] {
            color: $default-heading-color;
            background: none;
        }

        .select2-results__option {
            padding-left: 1em;

            .select2-results__group {
                padding-left: 0;
            }

            .select2-results__option {
                margin-left: -1em;
                padding-left: 2em;

                .select2-results__option {
                    margin-left: -2em;
                    padding-left: 3em;

                    .select2-results__option {
                        margin-left: -3em;
                        padding-left: 4em;

                        .select2-results__option {
                            margin-left: -4em;
                            padding-left: 5em;

                            .select2-results__option {
                                margin-left: -5em;
                                padding-left: 6em;
                            }
                        }
                    }
                }
            }
        }
    }

    .select2-results__option--highlighted {
        
        &[aria-selected] {
            color: $default-heading-color;
            background: none;
        }
    }

    .select2-results__group {
        display: block;
        padding: 7px 0;
        cursor: default;
    }
}

.select2-close-mask {
    position: fixed;
    top: 0;
    left: 0;
    width: auto;
    height: auto;
    min-height: 100%;
    min-width: 100%;
    display: block;
    margin: 0;
    padding: 0;
    background-color: #fff;
    border: 0;
    opacity: 0;
    filter: alpha(opacity=0);
    z-index: 99;
}

.select2-hidden-accessible {
    position: absolute !important;
    width: 1px !important;
    height: 1px !important;
    margin: -1px !important;
    padding: 0 !important;
    border: 0 !important;
    clip: rect(0 0 0 0) !important;
    overflow: hidden !important;
}

body.archive.post-type-archive.post-type-archive-product.woocommerce.woocommerce-page .select2-container--default .select2-results {
    text-align: right;
}

/* ==========================================================================
   Select 2 Script styles - end
   ========================================================================== */